	module.exports = "<h3 id=usage>Usage</h3><pre class=language-typescript><code class=language-typescript><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span>Timepicker<span class=\"token punctuation\">}</span> from <span class=\"token string\">'ng2-bootstrap'</span><span class=\"token punctuation\">;</span>\n</code></pre><h3 id=annotations>Annotations</h3><pre class=language-typescript><code class=language-typescript><span class=\"token comment\" spellcheck>// class Timepicker</span>\n@<span class=\"token function\">Component</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  selector<span class=\"token punctuation\">:</span> <span class=\"token string\">'timepicker[ng-model]'</span><span class=\"token punctuation\">,</span>\n  properties<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n    <span class=\"token string\">'hourStep'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'minuteStep'</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">'meridians'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'showMeridian'</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">'readonlyInput'</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">'mousewheel'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'arrowkeys'</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">'showSpinners'</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">'min'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'max'</span>\n  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n  lifecycle<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>LifecycleEvent<span class=\"token punctuation\">.</span>onInit<span class=\"token punctuation\">]</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n</code></pre><h3 id=timepicker-properties>Timepicker properties</h3><ul><li><code>vertical</code> (<code>?boolean=false</code>) - if <code>true</code> tabs will be placed vertically</li><li><code>justified</code> (<code>?boolean=false</code>) - if <code>true</code> tabs fill the container and have a consistent width</li><li><p><code>type</code> (<code>?string=&#39;tabs&#39;</code>) - navigation context class: &#39;tabs&#39; or &#39;pills&#39;</p></li><li><p><code>ng-model</code> (<code>*Date</code>) - binds to Date object</p></li><li><code>hour-step</code> (<code>?number=1</code>) - hours change step</li><li><code>minute-step</code> (<code>?number=1</code>) - minutes change step</li><li><code>meridians</code> (<code>?Array&lt;string&gt; = [&#39;AM&#39;, &#39;PM&#39;];</code>) - meridian labels based on locale (<em>will be based later</em>)</li><li><code>show-meridian</code> (<code>?boolean=true</code>) - if <code>true</code> works in 12H mode and displays AM/PM. If <code>false</code> works in 24H mode and hides AM/PM</li><li><code>readonly-input</code> (<code>?boolean=false</code>) - if <code>true</code> hours and minutes fields will be readonly</li><li><code>mousewheel</code> (<code>?boolean=true</code>) - if <code>true</code> scroll inside hours and minutes inputs will change time</li><li><code>arrowkeys</code> (<code>?boolean=true</code>) - if <code>true</code> up/down arrowkeys inside hours and minutes inputs will change time</li><li><code>show-spinners</code> (<code>?boolean=true</code>) - if <code>true</code> spinner arrows above and below the inputs will be shown</li><li><code>min</code> (<code>?Date:undefined</code>) - minimum time user can select</li><li><code>max</code> (<code>?Date:undefined</code>) - maximum time user can select</li></ul>";

