	module.exports = "<h1 id=getting-started>Getting started</h1><h3 id=first-of-all-welcome->First of all, Welcome!</h3><p>This module is currently in alfa while an Angular2 in alfa too ;)</p><p>It contains a lot of issues, no really a looot of issues, and it is a great chance to contribute and help us bring it to life!</p><p><strong>80%</strong> of baseline functionality is already <strong>rewritten</strong> to Angular2, currently Modal, Popover and Typeahed are left.</p><p><em>Note</em>: template-url and animation not yet implemented</p><p>While animations is just another feature, template-url implementation in Angular 2 seems no trivial task, more of that it has a chance to be against new Angular mental model.</p><p>Most probably template-url will be replaced by:</p><ol><li>temporal hack with dynamic components</li><li>template-ref, could became a standard</li><li>component extension with template overwriting</li></ol><h3 id=dependencies>Dependencies</h3><p>This module consists of native Angular2 components and directives, no jQuery or Bootstrap javascript is required.</p><p>Always actual list of JS dependencies you can find <a href=https://david-dm.org/valor-software/ng2-bootstrap>here</a></p><p>Plus this module plays nice with Bootstrap CSS <a href=\"http://getbootstrap.com/css/\">v3</a> and <a href=http://v4-alpha.getbootstrap.com>v4</a></p><p><em>Note</em> later on each component will be available independently via npm, jspm, etc.</p><h3 id=installation>Installation</h3><p>Currently preferable way to install this module is <code>npm</code>:</p><pre class=language-bash><code class=language-bash>npm <span class=\"token function\">install</span> <span class=\"token operator\">--</span>save ng2<span class=\"token operator\">-</span>bootstrap\n</code></pre><p>You may find angular2 bootstrap starter <a href=https://github.com/valor-software/angular2-bootstrap-starter>repo</a> useful, until we added plunker examples</p><p>Just 4 commands to start hacking Angular2 with bootstrap ;)</p><pre class=language-bash><code class=language-bash><span class=\"token function\">git</span> clone <span class=\"token function\">git</span>@github<span class=\"token punctuation\">.</span>com<span class=\"token punctuation\">:</span>valor<span class=\"token operator\">-</span>software<span class=\"token operator\">/</span>angular2<span class=\"token operator\">-</span>bootstrap<span class=\"token operator\">-</span>starter<span class=\"token punctuation\">.</span><span class=\"token function\">git</span>\n<span class=\"token function\">cd</span> angular2<span class=\"token operator\">-</span>bootstrap<span class=\"token operator\">-</span>starter\nnpm i <span class=\"token operator\">&amp;&amp;</span> npm start\n</code></pre><p>Have fun!</p><h3 id=reading-documentation>Reading documentation</h3><p>Each <code>ng2-bootstrap</code> component has api and annotation docs, examples and working demo. Each <code>property</code> and <code>event</code> has type annotation and default value if any.</p>";

